import React from "react";
import styled from 'styled-components';
import {CrossIcon, LeftArrowIcon, RightArrowIcon} from "../Icons";

const BottomSection = styled.div`
   width: ${props => props.imageWidth};
    
`

const Modal = styled.div`

@keyframes fade_text_caption {
  from {
      color: rgba(255, 255, 255, 0);
  }
  to {
      color: rgba(255, 255, 255, 1);
  }
}


@keyframes fade_text_desc {
  from {
      color: rgba(255, 255, 255, 0);
  }
  to {
      color: rgba(255, 255, 255, 0.7);
  }
}

@keyframes font_o {
  from {
      font-size: 0;
  }
  to {
      font-size: 0;
  }
}

@keyframes increase {
  from {
      height: 0;
      width: 0;
  }
  to {
      height: 85%;
      width: ${props => props.imageWidth};
  }
}

&.animated {

  animation-name: increase;
  animation-duration: 8s;
  animation-timing-function: ease;
  
  .magnify_modal_img_frame_bottom_caption, .magnify_modal_img_frame_bottom_text {
  
  
  animation-name: font_o;
  animation-duration: 8s;
  animation-timing-function: ease;
  
  
  
  
  }

  
  .magnify_modal_img_frame_bottom {
      
    animation-name: fade_text_caption;
    animation-duration: 8s;
    animation-delay: 8s;
    animation-timing-function: ease;
  }
  
  .magnify_modal_img_frame_bottom_text {
  
    animation-name: fade_text_caption;
    animation-duration: 8s;
    animation-delay: 8s;
    animation-timing-function: ease;
  }
  
    
}


  

`
// width: ${props => ((props.imageWidth + 1.4) + 'rem')};
class Preview extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      imageWidth: 0,
      animate: ''
    };
  }

  componentDidMount() {
    const img = this.refs.img;
    const imageRect = img.getClientRects()[0];
    const style = img.currentStyle || window.getComputedStyle(img);
    const margin = parseFloat(style.marginLeft) + parseFloat(style.marginRight);
    const imageContainerWidth = imageRect.width + margin;


    this.setState({
      imageWidth: imageContainerWidth,
      animate: 'animated'
    });
  }

  onCrossClick(e) {
    e.stopPropagation();
    this.props.close();
  }


  // click(e) {
  //   const clientRect = e.target.getClientRects()[0];
  //
  //   this.setState({
  //     magnify: 'magnify',
  //     animation: ''
  //   });
  // };

  // mouseLeave(e)  {
  //   !this.state.magnify && this.chooseAnimation(e, 'fade_out', 'from');
  // };
  //
  // mouseEnter(e)  {
  //   !this.state.magnify && this.chooseAnimation(e, 'fade_in', 'to');
  // };

  render() {
    const {src} = this.props;

    return (
        <div className='magnify_modal'>
          <Modal className={`magnify_modal_img_frame ${this.state.animate}`} imageWidth={this.state.imageWidth}>
            <div className='magnify_modal_img_frame_container'>
              <CrossIcon className='cross' onClick={(e) => this.onCrossClick(e)}/>
              <div className='left_arrow'>
                <LeftArrowIcon/>
              </div>
              <div className='right_arrow'>
                <RightArrowIcon/>
              </div>
              <div className='counter_container'>
                <div className='counter'>1 of 52</div>
              </div>
              <img src={src} className='image_preview' ref='img'/>
            </div>
            <BottomSection className='magnify_modal_img_frame_bottom' imageWidth={this.state.imageWidth}>
              <div className='magnify_modal_img_frame_bottom_caption'>Caption</div>
              <div className='magnify_modal_img_frame_bottom_text'>Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason. Some text which was provided for some reason.</div>
            </BottomSection>
          </Modal>
        </div>
    );
  }
}

export default Preview;


